/* LOADER */
._1hjhJ {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  box-sizing: content-box !important;
  -webkit-box-sizing: content-box !important;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}
._eafjx {
  display: flex;
  align-items: center;
  margin-bottom: 70px;
}
._3Wu7F {
  margin: 0px 5px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #777;
}
._1OrBQ {
  -webkit-animation: _3kSlr 1.2s ease 0s infinite alternate;
          animation: _3kSlr 1.2s ease 0s infinite alternate;
}
._2SSXc {
  -webkit-animation: _3kSlr 1.2s ease .4s infinite alternate;
          animation: _3kSlr 1.2s ease .4s infinite alternate;
}
._21K97 {
  -webkit-animation: _3kSlr 1.2s ease .8s infinite alternate;
          animation: _3kSlr 1.2s ease .8s infinite alternate;
}
._1WOdI{
  width: 300px;
  position: absolute;
  text-align: center;
  font-size: 18px;
}
._1J5um:hover{
  opacity: .7;
}
@-webkit-keyframes _3kSlr {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: .25;
    transform: scale(.75);
  }
}
@keyframes _3kSlr {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: .25;
    transform: scale(.75);
  }
}